//const url = "https://car79.github.io/AlboPretorioSolarinoApp/?idPub=";
const Pubblicazione = ({ pubblicazione, baseUrlAllegati }) => {
  pubblicazione.allegati.sort((a, b) => {
    a = a.nome.substring(a.nome.lastIndexOf(".")).toLowerCase();
    b = b.nome.substring(b.nome.lastIndexOf(".")).toLowerCase();
    return a > b ? -1 : a < b ? 1 : 0;
  });
  const url = "https://car79.github.io/AlboPretorioSolarinoApp/?idPub=";
  return (
    <div className="border mb-6 p-6 shadow-lg rounded-xl">
      <div className=" flex items-center pb-3">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          fill="none"
          viewBox="0 0 24 24"
          strokeWidth="1.5"
          stroke="currentColor"
          class="w-8 h-8"
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            d="M19.5 14.25v-2.625a3.375 3.375 0 00-3.375-3.375h-1.5A1.125 1.125 0 0113.5 7.125v-1.5a3.375 3.375 0 00-3.375-3.375H8.25m0 12.75h7.5m-7.5 3H12M10.5 2.25H5.625c-.621 0-1.125.504-1.125 1.125v17.25c0 .621.504 1.125 1.125 1.125h12.75c.621 0 1.125-.504 1.125-1.125V11.25a9 9 0 00-9-9z"
          />
        </svg>
        <div>
          <span className="font-bold ">{pubblicazione.num}</span>
          <span>/{pubblicazione.anno}</span>
        </div>
      </div>
      {pubblicazione.dettaglio}
      <div className="text-xs mt-2 text-slate-500">
        Pubblicazione: dal {pubblicazione.dataDa} al {pubblicazione.dataA}
      </div>
      <h1 className="font-bold py-3 text-blue-800">{pubblicazione.oggetto}</h1>
      <div className="pb-3 inline-block">
        <Condividi
          url={
            url +
            pubblicazione.anno +
            "" +
            String(pubblicazione.num).padStart(8, "0")
          }
          oggetto={pubblicazione.oggetto}
        />
      </div>
      <div className="flex flex-wrap text-sm ">
        {pubblicazione.allegati.map((allegato, index) => (
          <Allegato
            key={index}
            allegato={allegato}
            baseUrlAllegati={baseUrlAllegati}
          />
        ))}
      </div>
    </div>
  );
};
export default Pubblicazione;
